import { about } from '../../portfolio'
import './About.css'

const About = () => {
  const { name, description, resume} = about

  return (
    <div className='about center'>
      {name && (
        <h1>
          Hi, I am <br/> 
          <span className='about__name'>{name}.</span>
        </h1>
      )}
      <p className='about__desc'>{description && description} {resume && (
          <a href={resume}>
            <span type='button' className='btn btn--outline custom-btn'>
              Resume
            </span>
          </a>
        )}</p>
    </div>
  )
}

export default About
